export class Section1 extends Component {
  constructor(){
    super();
    this.state = {
     isHovering: null,
     items:[],
     about: [ {
       image: "/images/placeholder/generic-image.png"
     }
    ],
     features: [ {
       image: "/images/placeholder/generic-image.png",
       title: "Loading...",
       description: " "
     }
    ],
     highlights: [ {
       image: "/images/placeholder/generic-image.png",
       title: "Loading...",
       description: " "
     },
     {
       image: "/images/placeholder/generic-image.png",
       title: "Loading...",
       description: " "
     }
   ],
   loaded: 'loading'
    };
  }
  componentDidMount(){
    this.setState({ hero: [ {
      image: "/images/placeholder/generic-image.png"
    }] });
    this.setState({ features:  [API.Highlight[3]] });
    this.setState({ highlights:  [API.Highlight[1],API.Highlight[2],API.Highlight[3],API.Highlight[4]] });
    this.setState({ about:  [API.About[1]] });
    this.setState({ loaded: 'loaded' });
  }
  componentWillUnmount(){
    this.setState({ loaded: 'cached' });
  }
  render() {
    const featuredProject = this.state.features.map((feature) =>
            <div key={feature.id} className="mb-3 featuredArticle h-100">
              <Link to={`/projects/${feature.id}-${feature.title}`}>
                <div className="featureImage">
                  <LazyLoad className="img-container w-100 position-relative"
                  debounce={false}
                  offsetVertical={0}
                  >
                    <img className={`w-100 ${this.state.loaded} img-padding position-absolute` }
                          alt={feature.alt}
                          src={`${feature.imageSmall}`}
                          srcSet={`${feature.imageSmall} 320w,
                                  ${feature.imageMedium} 768w,
                                  ${feature.imageLarge} 1024w,
                                  ${feature.imageXLarge} 1280w`}
                              sizes="
                          (min-width:    1px) and (max-width:  375px) calc(  100vw * (0.94 * 1.000) * 1.00 ),
                          (min-width:  376px) and (max-width:  767px) calc(  100vw * (0.94 * 0.905) * 0.50 ),
                          (min-width:  768px) and (max-width:  980px) calc(  100vw * (0.94 * 0.850) * 0.33 ),
                          (min-width:  981px) and calc(  100vw * (0.94 * 0.835) * 0.25 )"
                    />
                  </LazyLoad>
                  <div className="highlighted d-block mr-0">Featured</div>
                </div>
                <div className="">
                <h1 className="featured ml-auto mr-auto w-75 py-4 mt-2">{feature.title}</h1>
                <p className="feature ml-auto mr-auto w-75 text-center">{feature.description}</p>
                </div>
              </Link>
            </div>
    );
    const highlightProject = this.state.highlights.slice(0,2).map((highlight) =>
          <Link key={highlight.id} to={`/projects/${highlight.id}-${highlight.title}`}>
          <div className="mb-5 fillHeight">
            <div className="featureImage">
              <LazyLoad className="img-container w-100 position-relative"
              debounce={false}
              offsetVertical={0}
              >
              <img className={`w-100 ${this.state.loaded} img-padding position-absolute`} alt={highlight.alt}
                    src={`${highlight.imageSmall}`}
                    srcSet={`${highlight.imageSmall} 320w,
                            ${highlight.imageMedium} 768w,
                            ${highlight.imageLarge} 1024w,
                            ${highlight.imageXLarge} 1280w`}
                            sizes="
                        (min-width:    1px) and (max-width:  375px) calc(  100vw * (0.94 * 1.000) * 1.00 ),
                        (min-width:  376px) and (max-width:  767px) calc(  100vw * (0.94 * 0.905) * 0.50 ),
                        (min-width:  768px) and (max-width:  980px) calc(  100vw * (0.94 * 0.850) * 0.33 ),
                        (min-width:  981px) and calc(  100vw * (0.94 * 0.835) * 0.25 )"
                    />
              </LazyLoad>
              <div className="highlighted d-block mr-0">New</div>
            </div>
            <div className="">
              <h3 className="py-2 mb-0 mt-2">{highlight.title}</h3>
              <p className="grey">{highlight.description}</p>
            </div>
            <hr className="w-25 ml-0"/>
          </div>
          </Link>
    );
    const aboutMe = this.state.about.map((about) =>
            <div>
              <h3 className="mb-0">{about.title}</h3>
              <hr className="w-25 ml-0"/>
                <LazyLoad className="img-container w-100 position-relative"  key={1}
                debounce={false}
                offsetVertical={0}
                >
                <img className={`w-100 ${this.state.loaded} img-padding position-absolute`}
                      alt="Sumner delivering a presentation"
                      src={`${about.imageSmall}`}
                      srcSet={`${about.imageSmall} 320w,
                              ${about.imageMedium} 768w,
                              ${about.imageLarge} 1024w,
                              ${about.imageXLarge} 1280w`}
                          sizes="
                      (min-width:    1px) and (max-width:  375px) calc(  100vw * (0.94 * 1.000) * 1.00 ),
                      (min-width:  376px) and (max-width:  767px) calc(  100vw * (0.94 * 0.905) * 0.50 ),
                      (min-width:  768px) and (max-width:  980px) calc(  100vw * (0.94 * 0.850) * 0.33 ),
                      (min-width:  981px) and calc(  100vw * (0.94 * 0.835) * 0.25 )"
                />
                </LazyLoad>
              <div className="mb-5 fillHeight">
                <div className="mt-4">
                  <p className="grey">Hi, my name's <strong>Sumner</strong> and Im a Senior UI Designer/Developer who specialises in building efficient, engaging experiences.</p>
                </div>
                <hr className="w-25 ml-0"/>
                  <p className="grey">For a sample of what I can do, take a look around my site. If you're looking for something more specific, be sure to <a className="inline" target="blank" href="mailto:sumnerbhandal@gmail.com"> get in touch</a> and I'll get back to you as soon as possible.</p>
              </div>
            </div>
    );
    return (
        <div id="resources" className="container sectionOne">
          <div className="row">

            <div className={`col-md-6 col-lg-3 order-md-3 order-lg-3 px-4 mobileFeatured about-me-small`}>
                {aboutMe}
            </div>
            <div className="col-md-12 col-lg-6 order-md-1 order-lg-2 px-4 mobileFeatured mb-5">
              {featuredProject}
            </div>

            <div className="col-md-6 col-lg-3 order-md-2 order-lg-1 px-4 mobileFeatured  must-read">
              {highlightProject}
            </div>
          </div>
        </div>
    );
  }
}

export class LoadHeaders extends Component {
  constructor(){
    super();
    this.state = {
     highlights: []
    };
  }
  componentDidMount(){
    this.setState({ highlights:  [API.Highlight[1],API.Highlight[2],API.Highlight[3],API.Highlight[4]] });
  }
  render() {
    const headers = this.state.highlights.map((highlight) =>
              <img className="d-none" alt={highlight.alt} src={`${highlight.headerimage}w1280`} />
    );
    return (
        <div>
            {headers}
        </div>
    );
  }
}

